.nav {
  margin: 10px 0 10px 10px; }
  @media (min-width: 768px) {
    .nav {
      width: 100%; } }

.nav__list {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  position: absolute;
  display: flex;
  flex-direction: column;
  font-size: 18px;
  line-height: 22px;
  list-style: none;
  margin: 10px 0 0 -10px;
  padding: 0;
  background-color: #ffffff; }
  @media (min-width: 768px) {
    .nav__list {
      position: relative;
      background-color: transparent;
      padding: 0 0 0 172px;
      margin: 0;
      box-sizing: border-box;
      width: 98%;
      flex-direction: row; } }
  @media (min-width: 1300px) {
    .nav__list {
      padding: 0 0 0 35%;
      width: 60%;
      font-size: 18px;
      line-height: 22px;
      box-sizing: unset; } }
  @media (min-width: 1800px) {
    .nav__list {
      font-size: 22px; } }

@media (min-width: 768px) {
  .nav__item {
    padding-right: 25px;
    width: 25%; } }

.nav--closed .nav__toggle {
  position: relative;
  background-color: #802d25;
  border: 0;
  width: 25px;
  height: 25px; }
  .nav--closed .nav__toggle::before {
    content: "";
    position: absolute;
    background-image: url(../img/svg/menu-button.svg);
    width: 25px;
    height: 25px;
    top: 0;
    left: 0; }
  @media (min-width: 768px) {
    .nav--closed .nav__toggle {
      display: none; } }

.nav--opened .nav__toggle {
  position: relative;
  background-color: #802d25;
  border: 0;
  width: 25px;
  height: 25px; }
  .nav--opened .nav__toggle::before {
    content: "";
    position: absolute;
    background-image: url(../img/svg/menu-button.svg);
    width: 25px;
    height: 25px;
    top: 0;
    left: 0; }
  @media (min-width: 768px) {
    .nav--opened .nav__toggle {
      display: none; } }

@media (max-width: 767px) {
  .nav--closed .nav__list {
    display: none; } }

.nav__item-link {
  display: block;
  padding: 18px 0;
  border-top: 1px solid #e6e6e6; }
  @media (min-width: 768px) {
    .nav__item-link {
      border: 0;
      text-transform: initial;
      color: #ffffff; } }

.header {
